* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  direction: ltr;
  background: #fff;
  font-family: Bainsley, sans-serif;
  transition: background .9s;
  overflow-x: hidden !important;
}

.list-unstyled {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.hero {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.hero .hidden-xs {
  display: block;
}

.hero img {
  position: absolute;
}

.hero #bg {
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.hero #rock-right {
  z-index: 2;
  max-width: 300px;
  bottom: 0;
  right: 0;
  transform: translate3d(0, 0, 0);
}

.hero #man {
  z-index: 3;
  max-width: 500px;
  bottom: 0;
  left: 0;
  transform: translate3d(0, 0, 0);
}

.hero #mountain {
  z-index: 1;
  max-width: 600px;
  bottom: 0;
  right: 0;
  transform: translate3d(0, 0, 0);
}

.hero #forest-right {
  z-index: 1;
  bottom: 0;
  left: 50%;
  transform: translate3d(0, 100%, 0);
}

.hero #forest-left {
  z-index: 1;
  max-width: 700px;
  bottom: 0;
  left: 0;
  transform: translate3d(0, 100%, 0);
}

.hero__typewrap {
  z-index: 2;
  color: #000;
  text-transform: capitalize;
  justify-content: center;
  align-items: center;
  min-width: 500px;
  display: flex;
  position: absolute;
  top: 200px;
  left: 50%;
}

.hero__typewrap h1 {
  text-transform: uppercase;
  min-width: 500px;
  font-size: 30px;
}

.hero__typewrap .typed {
  color: #5f2d4a;
  text-transform: uppercase;
  font-size: 35px;
  font-weight: bold;
}

.hero .menu {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.hero .menu li a {
  color: #fff;
  background-color: #0006;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  min-width: 80px;
  height: 30px;
  margin: 0 5px;
  padding: 5px;
  font-size: 14px;
  font-weight: bold;
  display: inline-flex;
}

.hero .socials {
  z-index: 4;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 40%;
  left: 20px;
  transform: translateY(-50%);
}

.hero .socials li a {
  background-color: #0006;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 5px 0;
  padding: 5px;
  font-weight: bold;
  display: inline-flex;
}

.hero .socials li a svg path {
  fill: #fff;
}

.identity {
  background-color: #422b26;
  width: 100%;
  height: 100vh;
  position: relative;
}

.identity .clouds {
  z-index: 1;
  position: absolute;
}

.identity .clouds.right.top {
  animation: 200s linear infinite alternate moveCloudsForthAndBack;
  top: 50%;
  left: 0;
  right: auto;
}

.identity .clouds.center.top {
  animation: 100s linear infinite alternate moveCloudsForthAndBack;
  top: 50%;
  left: 50%;
  right: auto;
}

.identity .clouds.bottom.left {
  animation: 300s linear infinite alternate moveCloudsForthAndBack;
  inset: auto auto 20px 0;
}

.identity .identityInner {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  display: flex;
}

.identity .identityInner__title {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  z-index: 2;
  margin-bottom: 5px;
  font-size: 40px;
  position: relative;
}

.identity .identityInner__title .name {
  color: #fff;
  font-size: 50px;
  font-weight: bold;
}

.identity .identityInner__text {
  color: #fff;
  text-transform: capitalize;
  text-align: center;
  z-index: 2;
  max-width: 500px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  font-size: 14px;
  position: relative;
}

.identity .identityInner__text .info {
  text-transform: capitalize;
  font-size: 40px;
}

.identity .identityInner h2 {
  color: #fff;
  z-index: 2;
  margin-top: 10px;
  font-size: 16px;
  font-weight: 400;
  position: relative;
}

.identity .identityInner .mouse {
  z-index: 2;
  border: 2px solid #fff;
  border-radius: 50px;
  width: 30px;
  height: 50px;
  margin: 0 auto;
  display: block;
  position: relative;
}

.identity .identityInner .move {
  background-color: #fff;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  animation: 2s linear infinite move;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.identity .characterWrap {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.drawSec {
  background-image: linear-gradient(to right, #ffecd2 0%, #fcb69f 100%);
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.drawSec .line-container {
  text-align: center;
  width: 100%;
  height: 100%;
  padding: 100px 0;
  overflow: hidden;
}

.drawSec .line-container svg {
  height: 100%;
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  transform: scaleX(-1);
}

.drawSec .paper-plane {
  -o-object-fit: cover;
  object-fit: cover;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 0%;
}

.drawSec .scrollMoreText {
  text-align: center;
  color: #422b26;
  text-transform: uppercase;
  font-size: 60px;
  font-weight: bold;
}

@keyframes moveCloudsBackAndForth {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(80vw);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes moveCloudsForthAndBack {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(80vw);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes move {
  0% {
    opacity: 0;
    transform: translate(-50%, 10px);
  }

  50% {
    opacity: 1;
    transform: translate(-50%, 20px);
  }

  100% {
    opacity: 0;
    transform: translate(-50%, 30px);
  }
}

@media screen and (width <= 991px) {
  .paper-plane {
    height: 100px;
    position: absolute;
    top: 100%;
    left: 0%;
  }
}

@media screen and (width <= 767px) {
  .hidden-xs {
    display: none !important;
  }

  .hero {
    background-image: url("mobile-background.bb5b6b5a.jpg");
    background-repeat: no-repeat;
  }

  .identityInner__title .name {
    font-size: 30px !important;
  }

  .hero__typewrap {
    min-width: 100%;
    padding-left: 20px;
    left: 50%;
    right: auto;
    transform: translateX(-50%);
  }
}
